'use client';

import { TonConnectButton, useTonAddress, useTonConnectUI } from '@tonconnect/ui-react';
import { useEffect, useState } from 'react';

export function WalletConnectButton() {
  const address = useTonAddress();
  const [tonConnectUI] = useTonConnectUI();
  const [balance, setBalance] = useState<string>('0');

  useEffect(() => {
    if (address) {
      // Fetch balance from blockchain
      fetchBalance(address);
    }
  }, [address]);

  const fetchBalance = async (addr: string) => {
    try {
      const response = await fetch(`https://tonapi.io/v2/accounts/${addr}`);
      const data = await response.json();
      const balanceNano = data.balance;
      const balanceTON = (parseInt(balanceNano) / 1e9).toFixed(2);
      setBalance(balanceTON);
    } catch (error) {
      console.error('Error fetching balance:', error);
    }
  };

  if (!address) {
    return (
      <div className="flex flex-col items-center gap-4">
        <TonConnectButton />
        <p className="text-gray-400 text-sm">Connect wallet to activate tables</p>
      </div>
    );
  }

  return (
    <div className="flex flex-col items-center gap-4 p-6 rounded-2xl bg-gradient-to-br from-blue-600/20 to-purple-600/20">
      <div className="flex items-center gap-3">
        <div className="w-3 h-3 rounded-full bg-green-400 animate-pulse" />
        <span className="text-white font-bold text-xl">Wallet Connected</span>
      </div>
      
      <div className="text-center">
        <div className="text-gray-400 text-sm mb-1">Address</div>
        <div className="text-white font-mono text-sm">
          {address.slice(0, 8)}...{address.slice(-8)}
        </div>
      </div>

      <div className="text-center">
        <div className="text-gray-400 text-sm mb-1">Balance</div>
        <div className="text-white font-bold text-3xl">{balance} TON</div>
      </div>

      <button
        onClick={() => tonConnectUI.disconnect()}
        className="px-6 py-3 rounded-xl bg-red-500/20 hover:bg-red-500/30 text-red-400 font-bold transition-all"
      >
        Disconnect
      </button>
    </div>
  );
}
